import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import { Carousel, Flex, WingBlank, WhiteSpace, SearchBar } from 'antd-mobile';
import { getHomeGoodslist } from '../api/index'
import qs from 'querystring'
import '../style/home.css'
import banner1 from '../upload/banner1.png'
import banner2 from '../upload/banner2.png'
import banner3 from '../upload/banner3.png'

import { baseUrl } from '../api/index';

export class Home extends Component {
    constructor(props) {
        super(props)

        this.state = {

            imgHeight: '',
            // 
            goodsList: [],
            // 
            bottom: false,
            // 
            placeholderPre: ''
        }
    }
    // render
    UNSAFE_componentWillMount() {
        // 
        getHomeGoodslist().then(res => {
            // 
            const { message, meta: { status } } = res.data
            // 200
            if (status === 200) {
                //  16 => 4 * 40~15
                const index = Math.floor((Math.random() * 16))
                // 
                const i = Math.floor(index / 4)
                const j = index % 4

                this.setState({
                    // goodsList
                    goodsList: message,
                    // 
                    placeholderPre: message[i].goods[j].goods_name.slice(0, 10) + '...',
                    // 
                    bottom: true
                })
            }
        })
    }
    // 
    handleSearchSimilar = cid => {
        this.props.history.push('/searchgoods/' + qs.stringify({ cid }))
    }
    render() {
        return (
            // 
            <div>
                {/*  */}
                {this.props.location.pathname === '/' ?
                    <SearchBar placeholder={this.state.placeholderPre}
                        onFocus={() => this.props.history.push('/searchfield')}
                        className="search-area"
                    /> : ''}
                {/*  */}
                <Carousel
                    autoplay={true}
                    infinite
                    style={{ marginTop: 44 }}
                >
                    <img src={banner1} alt="" />
                    <img src={banner2} alt="" />
                    <img src={banner3} alt="" />
                </Carousel>
                {/*  */}
                <div className="catitems">
                    <div onClick={() => this.props.history.push('/searchgoods/query=')}><img src={`${baseUrl}/pyg/icon_index_nav_4@2x.png`} alt="" /></div>

                    <div onClick={() => this.props.history.push('/searchgoods/query=')}><img src={`${baseUrl}/pyg/icon_index_nav_2@2x.png`} alt="" /></div>

                    <div onClick={() => this.props.history.push('/searchgoods/query=')}><img src={`${baseUrl}/pyg/icon_index_nav_1@2x.png`} alt="" /></div>

                    <div onClick={() => this.props.history.push('/searchgoods/query=')}><img src={`${baseUrl}/pyg/icon_index_nav_5@2x.png`} alt="" /></div>
                </div>
                {/*  */}
                <div className="goodsList">
                    {this.state.goodsList.map(item => (
                        <div key={item.group_img} className="goods">
                            {/* WhiteSpace size */}
                            <WhiteSpace size="sm" />
                            <img src={item.group_img} alt="" />
                            {/* WingBlank size */}
                            <WingBlank size="sm">
                                {/* flex */}
                                <Flex
                                    justify="between"
                                    wrap="wrap"
                                >
                                    {item.goods.map(v => (
                                        <div key={v.goods_id} className="good">
                                            <div className="good_content"
                                                onClick={() => this.props.history.push(`/goodsdetail/${v.goods_id}`)}
                                            >
                                                <img src={v.goods_small_logo} alt="" />
                                                <div className="describe ellipsis-1">{v.goods_name}</div>
                                                <div className="price">&yen;{v.goods_price}</div>
                                            </div>
                                            <button
                                                className='search-similar'
                                                onClick={() => this.handleSearchSimilar(v.cat_id)}
                                            >
                                                
                                            </button>
                                        </div>
                                    ))}
                                </Flex>
                            </WingBlank>
                        </div>
                    ))
                    }

                </div>
                {/* bottom */}
                {this.state.bottom ? <div className="goods-list-bottom">
                    <div className="line">
                        <span>~</span>
                    </div>

                </div> : ''}

            </div>
        )
    }
}

export default withRouter(Home)
